.section-about .title-vertical {
    position: absolute;
    letter-spacing: 0;
    transform: rotate(-90deg);
    top: 112px;
    right: -50px;
    font-size: 1.8rem;
    width: 300px;
    font-weight: 700;
    text-align: right;
}

.about-img-box {
    padding-left: 1rem;
    position: relative;
}

@media (min-width: 576px) {
    .about-img-box {
        padding-left: 4rem;
    }
}

@media (min-width: 768px) {
    .about-img-box {
        padding-left: 5rem;
    }
}

.sinse-box {
    background-color: #2eca6a;
    padding: 1rem 2rem;
    position: absolute;
    left: 15px;
    bottom: 10px;
}

@media (min-width: 576px) {
    .sinse-box {
        padding: 1.5rem 2.5rem;
        bottom: 20px;
    }
}

@media (min-width: 767px) {
    .sinse-box {
        padding: 1.5rem 3rem;
    }
}

@media (min-width: 992px) {
    .sinse-box {
        padding: 3rem 4.5rem;
    }
}

.sinse-box .sinse-title {
    font-weight: 700;
    letter-spacing: 0.043em;
    font-size: 1rem;
}

@media (min-width: 576px) {
    .sinse-box .sinse-title {
        font-size: 1.3rem;
    }
}

@media (min-width: 768px) {
    .sinse-box .sinse-title {
        font-size: 1.8rem;
    }
}

@media (min-width: 992px) {
    .sinse-box .sinse-title {
        font-size: 2.2rem;
    }
}

.sinse-box .sinse-title span {
    position: absolute;
}

.sinse-box .sinse-title span::after {
    content: '';
    width: 35px;
    height: 2px;
    background-color: #000;
    position: absolute;
    bottom: -15px;
}

.sinse-box p {
    margin-bottom: 0;
    font-size: 0.7rem;
}

@media (min-width: 576px) {
    .sinse-box p {
        font-size: 0.8rem;
    }
}

@media (min-width: 767px) {
    .sinse-box p {
        font-size: 1rem;
    }
}













